import React from "react"

import { Link } from "gatsby"

const Header = () => {
  return (
    <div>
      <h1>
        <Link to="/">tbd</Link>
      </h1>
      <ul>
        <li>
          <Link to="/">sample</Link>
        </li>
      </ul>
    </div>
  )
}

export default Header
